export default {
	name:"ClassAItem",
	props: {
		list:null
	},
	data() {
		return {
			img:''
		}
	},
	methods: {
		
	},
	computed: {
		/* getStatu() {
			if(this.list.contract_status_name === 0) {
				return '在线'
			}else if(this.list.contract_status_name === 1) {
				return '离线'
			}
		}, */
		getGrade() {
			if(this.list.member_type === "0") {
				return '../../../../../static/img/team/v1.png'
			}else if(this.list.grade === "1") {
				return '../../../../../static/img/team/v2.png'
			}else if(this.list.grade === "2") {
				return '../../../../../static/img/team/v3.png'
			}else if(this.list.grade === "3") {
				return '../../../../../static/img/team/v4.png'
			}else if(this.list.grade === "4") {
				return '../../../../../static/img/team/v5.png'
			}
		},
		getGradeText() {
			if(this.list.grade === 0) {
				return '普通会员'
			}else if(this.list.grade === 6) {
				/* this.img = ''; */
				return '市场节点'
			}else if(this.list.grade === 7) {
				/* this.img = ''; */
				return '超级节点'
			}
		}
	}
}
